<template>
  <div class="page" id="question">
    <mt-header class="bar-nav" title="问题反馈" >
      <mt-button slot="left" icon="back" v-back-link></mt-button>
      <mt-button slot="right" class="right-icon">提交</mt-button>
    </mt-header>
    <div class="question">
      <mt-field placeholder="请在这里描述你所遇到的问题，非常感谢您的反馈！" v-model="con"  type="textarea" rows="4" :attr="{maxlength: 200}"><div class="mint-cell-right">{{con.length}}/200</div></mt-field>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'question',
    data() {
      return {
        con: ''
      }
    },
    mounted(){
      let st = document.getElementsByClassName('mint-field-core')[0]
      st.style.height = '1.6rem'
    }
  }
</script>
<style scoped>
  .question .mint-field-core {
    font-size: .15rem;
  }
  .mint-cell-right {
    height: auto;
    right: .7rem;
    top: 1.5rem;
    font-size: .15rem;
    color: #999;
  }
  .mint-button-text{
    font-size: .15rem;
    color: #999;
  }
</style>



